'use strict';

// Ð Ð¾Ð´Ð¸Ñ‚ÐµÐ»ÑŒÑÐºÐ¸Ð¹ ÐºÐ¾Ð½ÑÑ‚Ñ€ÑƒÐºÑ‚Ð¾Ñ€ Ð´Ð»Ñ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ñ‹Ñ… Ð¾ÐºÐ¾Ð½

(function () {
  var Modal = function (elem) {
    this.$modal = elem; // ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ð¾Ð³Ð¾ Ð¾ÐºÐ½Ð°
    this.overlay = document.querySelector('.overlay'); // ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚ Ð¿Ð¾Ð´Ð»Ð¾Ð¶ÐºÐ¸ Ð·Ð°Ñ‚ÐµÐ¼Ð½ÐµÐ½Ð¸Ñ
    this.buttonOpen = null; // ÐºÐ½Ð¾Ð¿ÐºÐ° Ð¾Ñ‚ÐºÑ€Ñ‹Ñ‚Ð¸Ñ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ð¾Ð³Ð¾ Ð¾ÐºÐ½Ð°
    this.buttonClose = this.$modal.querySelector('.modal__close'); // ÐºÐ½Ð¾Ð¿ÐºÐ° Ð·Ð°ÐºÑ€Ñ‹Ñ‚Ð¸Ñ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ð¾Ð³Ð¾ Ð¾ÐºÐ½Ð°
  };

  // Ð—Ð°Ð¿ÑƒÑÐº Ð»Ð¾Ð³Ð¸ÐºÐ¸
  Modal.prototype.start = function () {
    this.buttonClose.addEventListener('click', this.onCloseModal.bind(this));
  };

  // ÐžÑ‚ÐºÑ€Ñ‹Ñ‚Ð¸Ðµ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ð¾Ð³Ð¾ Ð¾ÐºÐ½Ð°
  Modal.prototype.open = function () {
    this.$modal.classList.remove('hidden');
    this.overlay.classList.remove('hidden');
  };

  // Ð—Ð°ÐºÑ€Ñ‹Ñ‚Ð¸Ðµ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ð¾Ð³Ð¾ Ð¾ÐºÐ½Ð°
  Modal.prototype.close = function () {
    this.$modal.classList.add('hidden');
    this.overlay.classList.add('hidden');
  };

  // Ð¡Ð¾Ð±Ñ‹Ñ‚Ð¸Ðµ Ð·Ð°ÐºÑ€Ñ‹Ñ‚Ð¸Ñ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ð¾Ð³Ð¾ Ð¾ÐºÐ½Ð°
  Modal.prototype.onCloseModal = function () {
    this.close();

    document.removeEventListener('keydown', this.onKeyPress.bind(this));
  };

  // Ð¡Ð¾Ð±Ñ‹Ñ‚Ð¸Ðµ Ð¾Ñ‚ÐºÑ€Ñ‹Ñ‚Ð¸Ñ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ð¾Ð³Ð¾ Ð¾ÐºÐ½Ð°
  Modal.prototype.onOpenModal = function (evt) {
    evt.preventDefault();

    this.open();
    document.addEventListener('keydown', this.onKeyPress.bind(this));
  };

  Modal.prototype.onKeyPress = function (evt) {
    var ESC = 27;

    if (evt.keyCode === ESC) {
      this.onCloseModal();
    }
  };

  window.Modal = Modal;
})();
