'use strict';

// Ð Ð¾Ð´Ð¸Ñ‚ÐµÐ»ÑŒÑÐºÐ¸Ð¹ ÐºÐ¾Ð½ÑÑ‚Ñ€ÑƒÐºÑ‚Ð¾Ñ€ Ð´Ð»Ñ Ñ„Ð¾Ñ€Ð¼

(function () {
  var Form = function (elem) {
    this.$form = elem; // ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚ Ñ„Ð¾Ñ€Ð¼Ñ‹

    this.$inputs = this.$form.elements; // Ð¿Ð¾Ð»Ñ Ñ„Ð¾Ñ€Ð¼Ñ‹
    this.$submit = this.$form.querySelector('button[type="submit"]'); // ÐºÐ½Ð¾Ð¿ÐºÐ° Ð¾Ñ‚Ð¿Ñ€Ð°Ð²ÐºÐ¸

    this.message = ''; // ÑÐ¾Ð¾Ð±Ñ‰ÐµÐ½Ð¸Ðµ Ð¾Ð± Ð¾ÑˆÐ¸Ð±ÐºÐµ
    this.Ñharacters = 0; // ÐºÐ¾Ð»Ð¸Ñ‡ÐµÑÑ‚Ð²Ð¾ ÑÐ¸Ð¼Ð²Ð¾Ð»Ð¾Ð² Ð²Ð²ÐµÐ´ÐµÐ½Ð½Ð¾Ðµ Ð¿Ð¾Ð»ÑŒÐ·Ð¾Ð²Ð°Ñ‚ÐµÐ»ÐµÐ¼

    this.start();
  };

  Form.prototype.start = function () {
    this.$submit.addEventListener('click', this.onEditForm.bind(this)); // Ð½Ð°Ð¶Ð°Ñ‚Ð¸Ðµ Ð½Ð° ÐºÐ½Ð¾Ð¿ÐºÑƒ Ð¾Ñ‚Ð¿Ñ€Ð°Ð²ÐºÐ¸ Ñ„Ð¾Ñ€Ð¼Ñ‹
    this.$form.addEventListener('submit', this.onSubmitForm.bind(this)); // Ð¾Ñ‚Ð¿Ñ€Ð°Ð²ÐºÐ° Ñ„Ð¾Ñ€Ð¼Ñ‹
  };

  // Ð¡Ð¾Ð±Ñ‹Ñ‚Ð¸Ðµ Ð¿Ñ€Ð¸ Ð¸Ð·Ð¼ÐµÐ½ÐµÐ½Ð¸Ð¸ Ð²Ð²Ð¾Ð´Ð° Ð² Ð¿Ð¾Ð»Ðµ
  Form.prototype.onEditForm = function () {
    this.onValidateForm();

    this.$form.addEventListener('input', this.onValidateForm.bind(this), true);
  };

  // Ð¡Ð¾Ð±Ñ‹Ñ‚Ð¸Ðµ Ð¾Ñ‚Ð¿Ñ€Ð°Ð²ÐºÐ¸ Ñ„Ð¾Ñ€Ð¼Ñ‹
  Form.prototype.onSubmitForm = function () {
    this.$form.removeEventListener('input', this.onEditForm.bind(this), true);
  };

  // Ð¡Ð¾Ð±Ñ‹Ñ‚Ð¸Ðµ Ð²Ð°Ð»Ð¸Ð´Ð°Ñ†Ð¸Ð¸ Ñ„Ð¾Ñ€Ð¼Ñ‹
  Form.prototype.onValidateForm = function () {
    // ÐšÐ¾Ð´..
  };

  // Ð£Ð²ÐµÐ´Ð¾Ð¼Ð»ÐµÐ½Ð¸Ðµ Ð¾Ð± Ð¾ÑˆÐ¸Ð±ÐºÐµ
  Form.prototype.alert = function (input) {
    input.setCustomValidity(this.message);
    this.effect(input);
  };

  // Ð­Ñ„Ñ„ÐµÐºÑ‚Ñ‹ Ð¿Ñ€Ð¸ Ð¾ÑˆÐ¸Ð±ÐºÐ¸
  Form.prototype.effect = function (input) {
    this.message ? input.classList.add('error') : input.classList.remove('error');
  };

  window.Form = Form;
})();
